<template>
  <div>
    <!-- 導航欄 -->
    <van-nav-bar
      title="商品列表"
    />
    <!-- 搜索框 -->
      <van-search show-action @click.stop="$router.push('/search')" :placeholder="searchValue || '請輸入搜尋的商品'">
        <template #action>
          <div @click="$router.push('/apps')"><van-icon name="apps-o" size="1.5em"/></div>
        </template>
      </van-search>
    <!-- 排序按鈕 -->
    <div class="sortTypeBox">
      <button v-if="sortType === 'allDown'" class="sortType" @click="proListAllUp">綜合Down</button>
      <button v-else-if="sortType === 'allUp'" class="sortType" @click="proListAllDown">綜合Up</button>
      <button v-else class="sortType" @click="proListAllDown">綜合</button>
      <button v-if="sortType === 'salesDown'" class="sortType" @click="proListSalesUp">銷售Down</button>
      <button v-else-if="sortType === 'salesUp'" class="sortType" @click="proListSalesDown">銷售Up</button>
      <button v-else class="sortType" @click="proListSalesDown">銷售</button>
      <button v-if="sortType === 'priceDown'" class="sortType" @click="proListPriceUp">價格Down</button>
      <button v-else-if="sortType === 'priceUp'" class="sortType" @click="proListPriceDown">價格Up</button>
      <button v-else class="sortType" @click="proListPriceDown">價格</button>
    </div>
    <!-- 商品列表 -->
    <div v-if="proList.length === 0">搜尋中...</div>
    <div v-else-if="proList[0] === 'error'">查無商品</div>
    <div v-else-if="proList.length > 0">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getProList } from '@/api/product'
export default {
  name: 'searchList',
  components: { GoodsItem },
  data () {
    return {
      searchValue: this.$route.query.search,
      page: 1,
      proList: [],
      sortType: 'allDown'
    }
  },
  async created () {
    const { data: { list } } = await getProList({
      categoryId: this.$route.query.categoryId, // 分類頁面的Id傳參
      goodsName: this.searchValue,
      page: this.page
    })
    if (list.total === 0) {
      this.proList = ['error']
      return
    }
    this.proList = list.data
    this.proListAllDown()
  },
  methods: {
    proListAllDown () {
      this.proList = this.proList.sort(function (a, b) {
        const allA = a.goods_sales * a.goods_price_min
        const allB = b.goods_sales * b.goods_price_min
        return allB - allA // b-a降續
      })
      this.sortType = 'allDown'
    },
    proListAllUp () {
      this.proList = this.proList.sort(function (a, b) {
        const allA = a.goods_sales * a.goods_price_min
        const allB = b.goods_sales * b.goods_price_min
        return allA - allB
      })
      this.sortType = 'allUp'
    },
    proListSalesDown () {
      this.proList = this.proList.sort(function (a, b) {
        const allA = a.goods_sales
        const allB = b.goods_sales
        return allB - allA
      })
      this.sortType = 'salesDown'
    },
    proListSalesUp () {
      this.proList = this.proList.sort(function (a, b) {
        const allA = a.goods_sales
        const allB = b.goods_sales
        return allA - allB
      })
      this.sortType = 'salesUp'
    },
    proListPriceDown () {
      this.proList = this.proList.sort(function (a, b) {
        const allA = a.goods_price_min
        const allB = b.goods_price_min
        return allB - allA
      })
      this.sortType = 'priceDown'
    },
    proListPriceUp () {
      this.proList = this.proList.sort(function (a, b) {
        const allA = a.goods_price_min
        const allB = b.goods_price_min
        return allA - allB
      })
      this.sortType = 'priceUp'
    }
  }
}
</script>

<style lang="less" scoped>
.sortTypeBox{
  display: flex;
  margin: 0 5px;
  flex-wrap: wrap;
  width: calc(100% - 10px);
  .sortType{
    width: calc(33.33% - 10px);
    margin: 5px;
    padding: 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000;
    border-radius: 3px;
  }
}
</style>
